.gpt3__features {
     width: 100%;
     display: flex;
     justify-content: space-between;
     flex-wrap: wrap;
}

.gpt3__features-heading {
     flex: 1;
     display: flex;
     flex-direction: column;
     justify-content: flex-start;
     align-items: flex-start;
     margin-right: 5rem;
}

.gpt3__features-heading h1 {
     font-family: var(--font-family);
     font-style: normal;
     font-weight: 800;
     font-size: 34px;
     line-height: 45px;
     margin-bottom: 2rem;
}

.gpt3__features-heading p {
     font-family: var(--font-family);
     font-style: normal;
     font-weight: 500;
     font-size: 16px;
     line-height: 30px;
     color: #FF8A71;
}

.gpt3__features-container {
     flex: 1.5;
     display: flex;
     flex-direction: column;
     justify-content: flex-start;
     align-items: center;
}

.gpt3__features .gpt3__feature__container p {
     padding: 0 2rem;
}

@media screen and (max-width:990px) {
     .gpt3__features {
          flex-direction: column;
     }

     .gpt3__features-heading {
          margin: 0 0 3rem 0;
     }

     .gpt3__features .gpt3__feature__container {
          margin-bottom: 4rem;
     }

     .gpt3__features .gpt3__feature__container p {
          padding: 0 2rem;

     }
}

@media screen and (max-width:767px) {
     .gpt3__features .gpt3__feature__container p {
          padding: 0 2rem 0 0;

     }
}

@media screen and (max-width:550px) {
     .gpt3__features-heading h1 {
          font-size: 28px;
          line-height: 38px;
     }

     .gpt3__features-heading p {
          font-size: 16px;
          line-height: 24px;
          color: #FF8A71;
     }
}