.gpt3__possibility {
     width: 100%;
     display: flex;
     flex-direction: row;
     padding-top: 0 !important;
}

.gpt3__possibility-image {
     flex: 1;
}

.gpt3__possibility-image img {
     width: 100%;
     height: 100%;
}

.gpt3__possibility-text {
     flex: 1;
     padding: 0 1.5rem;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: flex-start;
}

.gpt3__possibility-text h4 {
     font-family: var(--font-family);
     font-weight: 500;
     font-size: 16px;
     line-height: 30px;
     color: #71E5FF;
}

.gpt3__possibility-text h1 {
     font-family: var(--font-family);
     font-weight: 800;
     font-size: 34px;
     line-height: 45px;
     margin: 1rem 0;
}

.gpt3__possibility-text p {
     font-family: var(--font-family);
     font-weight: 400;
     font-size: 16px;
     line-height: 30px;
     color: var(--color-text);
     margin-bottom: 2rem;
}

.gpt3__possibility-text h4:last-child {
     color: #FF8A71;
}

@media screen and (max-width:950px) {
     .gpt3__possibility {
          flex-direction: column;
     }
     .gpt3__possibility-image {
          margin: 1rem 0;
     }
     .gpt3__possibility-text {
          padding: 0;
          margin-top: 2rem;
     }
}
@media screen and (max-width:550px) {
     .gpt3__possibility-text h1 {
          font-size: 28px;
          line-height: 38px;
     }
     .gpt3__possibility-text p {
          font-size: 14px;
          line-height: 24px;
     }
}