.gpt3__article {
     width: 100%;
     height: 100%;
     display: flex;
     flex-direction: column;
     margin-bottom: 2rem;
     background-color: #042C54;
}
.gpt3__article-image {
     width: 100%;
     height: 100%;
     background: var(--color-footer);
}
.gpt3__article img {
     width: 100%;
     height: 100%;

}

.gpt3__article-content {
     display: flex;
     flex-direction: column;
     justify-content: space-between;
     min-height: fit-content;
     padding: 1rem 1.5rem;
     height: 100%;
}

.gpt3__article-content p {
     font-family: var(--font-family);
     font-weight: 700;
     font-size: 11.649px;
     line-height: 35px;
     color: #FFFFFF;
}

.gpt3__article-content h4 {
     font-family: var(--font-family);
     font-weight: 800;
     font-size: 25.11px;
     line-height: 30px;
     color: #FFFFFF;
     margin-bottom: 5rem;
     cursor: pointer;
}

.gpt3__article-content button {
     font-family: var(--font-family);
     font-style: normal;
     font-weight: 700;
     font-size: 11.649px;
     line-height: 35px;
     color: #FFFFFF;
     background: transparent;
     border: none;
     cursor: pointer;;
    
}
@media screen and (max-width: 550px) {
     .gpt3__article-content h4 {
          font-size: 18px;
          line-height: 25px;
     }    
}